@import '_variables.scss';

* {
   margin: 0px;
   padding: 0px;
   box-sizing: border-box;
}

html {
   font-family: $fontMain;
   background-color: black;
}

#backdrop {
   background-color: black;
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
}

#contentFrame {
   display: flex;
   justify-content: center;
   width: 1520px;
   background-color: $backdrop;
   h1 {
      color: $dk-red;
   }
}